import * as React from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faAddressCard } from "@fortawesome/free-solid-svg-icons"
import { lowerCase } from "lodash"

import {
  DetailedServiceCardPropsType,
  ShortServiceCardPropsType,
} from "../../utils/types"

export const DetailedServiceCard = ({
  badgeType,
  desc,
  label,
  onClick,
}: DetailedServiceCardPropsType) => {
  return (
    <div
      tabIndex={0}
      role="button"
      aria-pressed="false"
      onClick={onClick}
      onKeyDown={onClick}
      className="relative flex w-4/12 cursor-pointer flex-col rounded-lg border border-gray-200 bg-white p-4 shadow hover:bg-gray-100 md:max-w-xl md:flex-row dark:border-gray-700 dark:bg-gray-800 dark:hover:bg-gray-700"
    >
      {lowerCase(badgeType) === "new" && (
        <span className="absolute right-0 top-0 inline-flex items-center rounded-md bg-red-50 px-2 py-1 text-xs font-medium text-red-700 ring-1 ring-inset ring-red-600/10">
          New
        </span>
      )}

      {lowerCase(badgeType) === "soon" && (
        <span className="absolute right-0 top-0 inline-flex items-center rounded-md bg-blue-100 px-2 py-1 text-xs font-medium text-blue-800 ring-1 ring-inset ring-red-600/10 dark:bg-blue-900 dark:text-blue-300">
          Coming Soon
        </span>
      )}

      <FontAwesomeIcon icon={faAddressCard} fontSize={"48px"} />
      <div className="ml-8 flex flex-col justify-between">
        <h5 className="mb-2 text-lg font-bold tracking-tight text-gray-900 dark:text-white">
          {label}
        </h5>
        <p className="truncate-multiline mb-3 h-12 font-normal text-gray-700 dark:text-gray-400">
          {desc}
        </p>
      </div>
    </div>
  )
}

